import React, { useContext, useEffect, useState } from 'react';

//  Componentes MUI
import { Button, Card, TextField, Typography } from '@mui/material';
import { CirclePicker } from 'react-color';

// Contexto de los datos de usuario
import { UserDataContext } from '../../../../context/UserDataContext';

//Componentes
import AccordionMenu from '../AccordionMenu/AccordionMenu';

//JSON Data personajes
import { hombreData, mujerData } from '../../../../context/CharacterModelData';

function PersonalizationMenu( { avatarType } ) {

  const { userData, setUserData } = useContext(UserDataContext); // Contexto de los datos de usuario

  const [userName, setUserName] = React.useState("");
  const handleChangeUserName = (event) => {
    setUserName(event.target.value);
  }

  const [ expanded, setExpanded ] = useState(false);          // Determina el Acordeon que est abierto para cerrar los dems
  const [ skinColor, setSkinColor ] = useState('#cc8860');    // Color de la piel 
  
  // Manejo de eventos de cambio de color de piel
  const handleColorSkinChange = (color, event) => {
    
    setSkinColor(color.hex);

    const ColorEvent = new CustomEvent('change_color_skin', { 
      detail:{
        color: color.hex
      } 
    });
    document.dispatchEvent(ColorEvent);
  }

  useEffect(() => {
    //  Se envia el dato de la piel al inicio del componente para setear el color por defecto
    const UpdateSkinColorEvent = new CustomEvent('update_skin_color', { 
      detail:{colorPiel:skinColor}
    });
    document.dispatchEvent(UpdateSkinColorEvent);
    
  },[])

  const handleSbmit = () => {
    // Se guarda en el localstorage para cargar de nuevo en recargas de pgina o cambios de ruta
    // console.log('guardar y redirigir', userData)
    const UserDataWithUserName = {
      ...userData, 
      ['preferenciasUsuario']:{
        ...userData.preferenciasUsuario, 
        ['nombre']:userName
      }
    }

    setUserData(UserDataWithUserName);
    localStorage.setItem('preferenciasUsuario', JSON.stringify(UserDataWithUserName));
    window.location.href = "/experiencia";
  }



  if(avatarType){

    const jsonData = avatarType == "hombre"? hombreData : mujerData;
    return (
      <div>
        <TextField id="name-text-input" 
          error={!(userName && userName.trim())}
          label="Nombre" 
          value={userName}
          onChange={handleChangeUserName}
          helperText="Ingresa el nombre con el que quieres identificarte"
          variant="filled" color="primary"
          // variant="standard" 
          sx={{ my:3, textAlign:'center', justifyContent:'center' }}/>
  
        <AccordionMenu prendas={jsonData.ropa.cabellos} expanded={expanded === 'panelCabello'} panelName='panelCabello' panelLabel='Cabello' setExpanded={setExpanded}></AccordionMenu>
        <AccordionMenu prendas={jsonData.ropa.camisas} expanded={expanded === 'panelCamisa'} panelName='panelCamisa' panelLabel='Camisa' setExpanded={setExpanded}></AccordionMenu>
        <AccordionMenu prendas={jsonData.ropa.pantalones} expanded={expanded === 'panelPantalones'} panelName='panelPantalones' panelLabel='Pantalones' setExpanded={setExpanded}></AccordionMenu>
        <AccordionMenu prendas={jsonData.ropa.zapatos} expanded={expanded === 'panelZapatos'} panelName='panelZapatos' panelLabel='Zapatos' setExpanded={setExpanded}></AccordionMenu>
          
        <Card sx={{ width:'100%', p:2 }}>
          <Typography sx={{ mb:2 }} variant="button" display="block" gutterBottom>
            Color de piel
          </Typography>
          <CirclePicker
            width = '100%'
            colors={[ "#d69f7e", "#c99271", "#cc8860", "#b0663a", "#7a411f", "#4a2108", "#1c0b01"]}
            color={ skinColor } 
            onChange={ handleColorSkinChange }
            onChangeComplete={ handleColorSkinChange }
            circleSize={25}
            circleSpacing ={15}
          />
        </Card>

        <Button 
          disabled={!(userName && userName.trim())} 
          variant="outlined" 
          sx={{mt:3}} 
          onClick={handleSbmit}>
            Continuar
        </Button>
        
      </div>
    );
  }
  else{
    <></>
  }
  
}
  
export default PersonalizationMenu;