/**
 * Componente principal de la vista de personalizacin
 * Incluido en los entries/chunks (webpack.config) para poder inyectarse en el html de personalizacion
 */

import React, { useEffect, useRef, useState } from 'react';
import ReactDOM from "react-dom/client";
import './style.css';

//  Importo los componentes de AFRAME que se usarn en toda la escena AFRAME
require('./components/aframe-components/scene-loaded');
require('./components/aframe-components/Personalization/select-avatar');
require('./components/aframe-components/Personalization/character-textures');
require('./components/aframe-components/Personalization/character-personalization');
require('../modules/aframe-orbit-controls-component');

//  Importo componentes externos
import { Box, FormControlLabel, Grid, Switch } from '@mui/material';

//  Importo componentes React
import GenderModal from './components/react-components/personalization/GenderModal/GenderModal';
import PersonalizationMenu from './components/react-components/personalization/PersonalizationMenu/PersonalizationMenu';
import LoadingPanel from './components/react-components/LoadingPanel/LoadingPanel';

//  Contextos
import { UserDataProvider } from './context/UserDataContext';
import { ContextTemplateProvider } from "./components/react-components/ContextTemplate";

// Creacin y renderizado del componente
const styleContainer = {
    justifyContent: 'center',
    alignItems:'center',
    textAlign: 'center'
};

const Personalization = () => {

    const [openModal, setOpenModal] = useState(false); 
    const [checkedAutoRotate, setcheckedAutoRotate] = useState(false);
    const [avatarType, setavatarType] = useState(null);
    const [playerEntity, setPlayerEntity] = useState(null);
    const [avatar3DLoaded, setAvatar3DLoaded] = useState(false);

    const container3D = useRef(null);

    //  Clono la escena para pasarla al componente de React y elimino la escena del body html
    useEffect(()=>{
        const aframe_scene = document.getElementById('scene-3D');
        const scene_clone = aframe_scene.cloneNode(true);

        aframe_scene.parentNode.removeChild(aframe_scene);
        container3D.current.appendChild(scene_clone);
        
        // Agrego el evenlistener de la escena monatada que se emite desde scene-loaded
        scene_clone.addEventListener('aframe-scene-loaded', (e)=>{
            setOpenModal(true);
        })

        // recupero la entidad del avatar y la almaceno en un estado 
        setPlayerEntity(document.getElementById('player-model'));

    }, [container3D.current]);


    // Espero al cambio de estado del tipo de avatar seleccionado
    useEffect(()=>{

        if(avatarType){
            if(playerEntity){
                //  cargo el atributo para montar el modelo en la entidad
                playerEntity.setAttribute('select-avatar', {avatarType: avatarType});
    
                //  Una vez cargado el modelo se escucha el evento que se emite en select-avatar.js
                playerEntity.addEventListener('avatar-personalization-loaded', (e) => {
                    console.log('modelo cargado', e.detail.loaded);
                    setAvatar3DLoaded(e.detail.loaded);
                }, false);
            }
        }

    }, [avatarType]);


    const handleChangeAutoRotate = (event) => {
        setcheckedAutoRotate(event.target.checked);

        //  Activo o desactivo la rotacin en la entidad de la cmara de AFRAME
        const cameraEntity = document.getElementById('personalization-camera');
        if(cameraEntity){
            cameraEntity.setAttribute('orbit-controls', {autoRotate: event.target.checked});
        }
    };

    return( 

        <Grid className='interactable-ui' sx={styleContainer} p={3} container spacing={3} >
            
            <Grid item md={6} xs={12} p={3} sx={{ height: '90vh' }}>
                    
                <div  style={{width:'100%', height:'100%'}} 
                    ref={container3D}>
                </div>

                {/* Si se seleccion avatar y carg el modelo se muestra el toggle de rotacin
                Si no ha cargado an, se muestra el panel de carga */}
                {avatarType && avatar3DLoaded &&
                    
                    <FormControlLabel control={<Switch />} 
                        checked={checkedAutoRotate}
                        onChange={handleChangeAutoRotate}
                        label="Rotacin Auntomtica" 
                        sx={{position:'absolute'}}/>
                }
            </Grid>

            <Grid item md={6} xs={12} sx={{ height: '100%' }}>
                {avatarType && avatar3DLoaded &&
                    <PersonalizationMenu avatarType={ avatarType }/>
                }
            </Grid>

            {!avatarType && !avatar3DLoaded && 
                <Box sx={{ position:'absolute', width: '100%', height: '100%' }}>
                    <LoadingPanel/>
                </Box>
            }
            <GenderModal  openModal={openModal} setOpenModal={setOpenModal} setavatarType={setavatarType}/>
            
        </Grid>
    );
}

/*  El componente principal de React se debe cargar una vez el documento y los script han cargado
    Esto se puede cambiar en WEBPACK
    new HtmlWebpackPlugin({...scriptLoading: 'blocking',}),
    Si estuviese scriptLoading: 'defer' esto no es necesario pero Aframe 1.3.0 requiere 'blocking'.
    'blocking' carga los chunks (scripts) antes de que se cargue el documento, 
    as se reconocen las etiquetas AFRAME
*/
document.addEventListener('DOMContentLoaded', activate);

function activate () {

    const root = ReactDOM.createRoot(document.getElementById("root"));
    root.render(
    //   <React.StrictMode>
          <ContextTemplateProvider>
            <UserDataProvider>
                <Personalization />
            </UserDataProvider>
            
          </ContextTemplateProvider>
      //</React.StrictMode>
    );
};

