/**
 * Componente Aframe para gestionar los cambios de personalizacin del personaje
 * Los cambios se leen en react y se leen en este componente para aplicarlos al modelo 3D
 */

AFRAME.registerComponent('character-personalization', {

    init: function() { 

        this.el.addEventListener('model-loaded', () => {

            const obj = this.el.getObject3D('mesh');
            let GROUP_NAME_SELECTED = null;
            let MESH_NAME_SELECTED = null;
            let MATERIAL_SELECTED = null;
            var PIEL_MATERIAL =null;

            let CEJAS_MATERIAL =null;
            var CABELLO_MATERIAL =null;

            let cabellos = null;
            let camisas = null;
            let pantalones = null;
            let zapatos = null;

            obj.traverse(function (node) {

                switch (node.name) {
                    case 'Cabellos':
                      cabellos = node;
                      break;
                    case 'Camisas':
                      camisas = node;
                      break;
                    case 'Pantalones':
                      pantalones = node;
                      break;
                    case 'Zapatos':
                      zapatos = node;
                      break;
                }

                if (node.material) {
                    if(node.material.name === "Piel_Color"){
                        PIEL_MATERIAL = node.material;
                    }
                    if(node.material.name === "Cejas_Color"){
                        CEJAS_MATERIAL = node.material;
                    }
                    if(node.material.name === "Cabello_Color" || node.material.name === "Cabello_Color1"){
                        CABELLO_MATERIAL = node.material;
                    }
                }
            });

            if(CEJAS_MATERIAL && CABELLO_MATERIAL){
                CEJAS_MATERIAL.color.set(CABELLO_MATERIAL.color);
            }

            document.addEventListener('select_clothe', function (e) {
                // console.log(e.detail);
                const group = e.detail.grupo;
                const mesh = e.detail.prenda;
                const material = e.detail.material;
                const texturas = e.detail.texturas;

                // console.log("texturas", texturas);
                
                switch (group) {
                    case 'Cabello':
                        activeMesh( cabellos , mesh, material, texturas , group);
                      break;
                    case 'Camisa':
                        activeMesh( camisas , mesh, material, texturas, group);
                      break;
                    case 'Pantalones':
                        activeMesh( pantalones , mesh, material, texturas, group);
                      break;
                    case 'Zapatos':
                        activeMesh( zapatos , mesh, material, texturas, group);
                      break;
                }
            }, false);

            const activeMesh = ( meshesGroup ,meshName, materialName, texturas , gropuName) => {
                if(meshesGroup.children){
                    const mesh = meshesGroup.children.find(el => el.name == meshName);
                    let color_mesh = null;

                    if( mesh ){
                        meshesGroup.children.forEach(el => el.visible=false);
                        mesh.visible = true;
                        
                        GROUP_NAME_SELECTED = gropuName;
                        MESH_NAME_SELECTED = meshName;

                        //Seleccionar nodo del color para el mesh
                        mesh.traverse(function (node) {
                            if (node.material) {
                                if(node.material.name === materialName){
                                    MATERIAL_SELECTED = node.material;
                                    color_mesh =  node.material.color.getHexString(); //node.material.color;
                                    // console.log(node.material.color.getHexString())
                                    if(texturas){

                                        node.material.transparent = true;
                                        node.material.side = THREE.DoubleSide;

                                        if(texturas.base){
                                            node.material.map = texturas.base;
                                            node.material.needsUpdate = true;
                                            
                                        }
                                        if(texturas.normal){
                                            node.material.normalMap = texturas.normal;
                                        }
                                    }
                                    else{
                                        node.material.map = null;
                                        node.material.normalMap = null;
                                        node.material.needsUpdate = true;
                                    }
                                    
                                    
                                }
                            }
                        });

                        //  enviar los datos al contexto de react
                        sendDataToContext (gropuName, meshName, MATERIAL_SELECTED.name, color_mesh);
                    }
                }
            }

            //  Por medio de un evento se envan los datos de las prendas seleccionadas al contexto de React
            const sendDataToContext = (gropuName, prendaName, materialName, color) =>{
                const dataprenda = {
                    grupo: gropuName,
                    prenda: prendaName,
                    material: materialName,
                    color: color 
                }

                const SendUserPrefsContextEvent = new CustomEvent('update_user_prefs', { 
                    detail:dataprenda
                });
                document.dispatchEvent(SendUserPrefsContextEvent);
                
                
            }

            // Escucha el evento de cambio de color de React para aplicarlo al mesh seleccionado
            document.addEventListener('change_color_mesh', function (e) {
                const hexColor = e.detail.color;
                MATERIAL_SELECTED.color.set(hexColor);

                //Aplicar el color del cabello a las cejas
                if(CEJAS_MATERIAL && CABELLO_MATERIAL){
                    CEJAS_MATERIAL.color.set(CABELLO_MATERIAL.color);
                }

                // console.log('selected', GROUP_NAME_SELECTED + ' ' + MESH_NAME_SELECTED)

                sendDataToContext (GROUP_NAME_SELECTED, MESH_NAME_SELECTED, MATERIAL_SELECTED.name, hexColor);
                    
            }, false);

            // Escucha el evento de cambio de color de piel
            document.addEventListener('change_color_skin', function (e) {
                const hexColor = e.detail.color;
                PIEL_MATERIAL.color.set(hexColor);
                
                const UpdateSkinColorEvent = new CustomEvent('update_skin_color', { 
                    detail:{colorPiel:hexColor}
                });
                document.dispatchEvent(UpdateSkinColorEvent);

            }, false);

            // //Lanza el evento de carga del modelo para cargar las partes en React
            // const eventLoaded = new Event('model_personalization_loaded');
            // document.dispatchEvent(eventLoaded);

            //  Evento de carga del modelo que se escucha en personalization.js
            const loadedEvent = new CustomEvent('avatar-personalization-loaded', { 
                detail:{
                  loaded: true
                } 
            });
            this.el.dispatchEvent(loadedEvent);
            
        }); 
    },

});