/**
 * Componente para gestionar la eleccin del avatar 

 */

AFRAME.registerComponent('select-avatar', {

    schema: {
        avatarType: {type: 'string', default: 'hombre'}
    },

    init: function(){
        const el = this.el;

        el.setAttribute('gltf-model', '#model-asset-personaje-' + this.data.avatarType)
        el.setAttribute('character-textures', {avatarType: this.data.avatarType});
        el.setAttribute('character-personalization', '');

        //  Espero a la carga del modelo para activar animacin y emitir evento de cargado a React
        el.addEventListener('model-loaded', () => {
            el.setAttribute('animation-mixer', 'clip:idle; crossFadeDuration:0');

            // //  Evento de carga del modelo que se escucha en personalization.js
            // const loadedEvent = new CustomEvent('avatar-personalization-loaded', { 
            //     detail:{
            //       loaded: true
            //     } 
            // });
            // el.dispatchEvent(loadedEvent);
        });
    }
});
