import React, { useState, useEffect } from 'react';
import { SliderPicker, CirclePicker } from 'react-color';

import {Accordion,
        AccordionDetails, 
        AccordionSummary,
        RadioGroup,
        Container, 
        Typography} from '@mui/material';

import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

//Componentes
import RadioCard from '../RadioCard/RadioCard';

function AccordionMenu({ expanded,  panelName, setExpanded, panelLabel='', prendas={} }) {

    const [modelLoaded, setModelLoaded] = useState(false);  // Bandera de carga del modelo, sire para cargar los mens y seleccionar opciones iniciales
    const [value, setValue] = useState('');                 // Valor (mesh name) de la prenda seleccionada
    const [color, setColor] = useState('#fff');             // Color del colorpicker para la prenda seleccionada  

    //Se ejecuta al renderizarse el componente
    useEffect( ( ) => {

        //  selecciona la opcin default para que se refleje en el modelo
        setValue(prendas.default);
        setModelLoaded(true);
        dispatchEventSelect(prendas.default);

    } , []);
    
    // Se envia el evento de cambio de prenda cada vez que se cambia la seleccin
    useEffect( () => {
        dispatchEventSelect(value);
    }, [value]);

    // Manejo de evento de cierre y apertura del acordeon
    const handleChangeAccordion = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
        dispatchEventSelect(value);
    };

    // Manejo de eventos para el grupo de inputs radio (Cards de opciones de prendas)
    const handleRadioChange = (event) => {
        setValue(event.target.value);
    };

    const dispatchEventSelect = (prendaName) => {
        const SelectEvent = new CustomEvent('select_clothe', { 
            detail:{
              grupo: panelLabel, 
              prenda: prendaName,
              material: prendas.opciones.find(el => el.mesh === prendaName)?.material || null,
              texturas: prendas.opciones.find(el => el.mesh === prendaName)?.texturas || null
            } 
        });
        document.dispatchEvent(SelectEvent);

    }


    // Manejo de eventos de cambio de color
    const handleColorChange = (color, event) => {
        setColor(color.hex);
        const ColorEvent = new CustomEvent('change_color_mesh', { 
            detail:{
              color: color.hex
            } 
        });
        document.dispatchEvent(ColorEvent);
    }

    // Manejo de eventos de cambio de color
    const handleChangeColorComplete = (color) => {
        setColor(color.hex);
        const ColorEvent = new CustomEvent('change_color_mesh', { 
            detail:{
              color: color.hex
            } 
        });
        document.dispatchEvent(ColorEvent);
    };
  
    return (
        <Accordion expanded={expanded} onChange={handleChangeAccordion( panelName )}>
            <AccordionSummary
                sx={{textAlign: 'left'}}
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1bh-content"
                id="panel1bh-header"
            >
                    <Typography variant="button" sx={{ width: '40%', flexShrink: 0 }}>
                        {panelLabel}
                    </Typography>
                    <Typography variant='caption' sx={{ color: 'text.secondary' }}>
                    {prendas.opciones.find(el => el.mesh === value)?.nombre || 'elemento seleccionado'}
                    </Typography>
            </AccordionSummary>

            {/************Si el modelo ya carg se carga el men de opciones ************/}
            {modelLoaded &&
            <AccordionDetails>
                <RadioGroup
                    aria-labelledby="radio-buttons-group"
                    row
                    // defaultValue={prendas.default}
                    name="radio-buttons-group"
                    value={value}
                    onChange={handleRadioChange}>
                        
                    {prendas.opciones.length>0 && prendas.opciones.map((prenda, key) => {
                        // console.log(prenda)
                        return (
                            <RadioCard key={key} 
                                dataPrenda = { prenda } 
                                selectedValue={ value } >
                            </RadioCard>
                        );
                    })}

                </RadioGroup>

                {/************Si ya se seleccion una prenda se carga el colorPicker ************/}
                {value !== '' && 
                <>
                    <Container sx={{ mt:2 }} maxWidth="sm">
                        <SliderPicker 
                            color={ color } 
                            onChange={ handleColorChange }
                            onChangeComplete={ handleChangeColorComplete }/>
                    </Container>

                    <Container sx={{ mt:2, }} maxWidth="sm">
                        <CirclePicker
                            colors={[ "#000000", "#525252", "#969696", "#D9D9D9", "#FFFFFF"]}
                            color={ color } 
                            onChange={ handleColorChange }
                            onChangeComplete={ handleChangeColorComplete }
                            circleSize={20}
                            circleSpacing ={20}
                        />
                    </Container>
                </>
                }
                
            </AccordionDetails>}
        </Accordion>
    );
}
  
export default AccordionMenu;