/**
 * Modal para seleccionar el gnero del avatar
 * se activa al iniciar la vista de personalizacin
 */

import React, { useContext, useState } from 'react';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import Dialog from '@mui/material/Dialog';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import Slide from '@mui/material/Slide';

//  Contextos 
import { UserDataContext } from '../../../../context/UserDataContext';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function GenderModal({ openModal=false, setOpenModal, setavatarType }) {
  const { userData, setUserData } = useContext(UserDataContext);

  // const [open, setOpen] = useState(openModal);
  const [valueGender, setValueGender] = useState(null);

  const handleChangeGender = (event) => {
    setValueGender(event.target.value);
    // console.log(userData)
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setavatarType(valueGender);
    setOpenModal(false);

    let allDataContext = {...userData};
    
    allDataContext.preferenciasUsuario.avatar = valueGender;
    setUserData(allDataContext);
    
  };

  return (
    <div>
      <Dialog
        // fullScreen
        fullWidth={true}
        maxWidth={'sm'}
        open={openModal}
        // onClose={handleClose}
        TransitionComponent={Transition}>
        
        <Container maxWidth="sm" sx={{height:'100%', display:'flex', justifyContent:'center', alignItems:'center', my:3}}>
          <form onSubmit={handleSubmit}>
            <FormControl>
              <FormLabel id="demo-controlled-radio-buttons-group">Selecciona el tipo de avatar que quieres que te represente</FormLabel>
              <RadioGroup
                aria-labelledby="demo-controlled-radio-buttons-group"
                name="controlled-radio-buttons-group"
                value={valueGender}
                onChange={handleChangeGender}
              >
                <FormControlLabel value="mujer" control={<Radio />} label="Mujer" />
                <FormControlLabel value="hombre" control={<Radio />} label="Hombre" />
              </RadioGroup>

              <Button disabled={!valueGender} sx={{ mt: 1, mr: 1 }} type="submit" variant="outlined">
                Seleccionar
              </Button>
            </FormControl>
          </form>
          
        </Container>

      </Dialog>
    </div>
  );
}