import React, { useState, useRef } from 'react';

import Card from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';
import FormControlLabel from '@mui/material/FormControlLabel';
import Radio from '@mui/material/Radio';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
import { alpha, styled } from '@mui/material/styles';

const CustomCardActionArea = styled(CardActionArea, {
  shouldForwardProp: (prop) => prop !== 'selected',
})(({ selected, theme }) => ({
  ...(selected && {
    color: theme.palette.primary.main,
    backgroundColor: alpha(theme.palette.primary.main, 0.2),
    border: `solid 4px ${alpha(theme.palette.primary.main, 0.3)}`,
    '& .MuiSlider-thumb': {
      [`&:hover, &.Mui-focusVisible`]: {
        boxShadow: `0px 0px 0px 8px ${alpha(theme.palette.primary.main, 0.16)}`,
      },
      [`&.Mui-active`]: {
        boxShadow: `0px 0px 0px 14px ${alpha(theme.palette.primary.main, 0.16)}`,
      },
    },
  }),
}));



export default function RadioCard({  selectedValue, dataPrenda }) {

  const radioRef = useRef(null);

  const handelClick = (  ) =>{
    radioRef.current.click();
  }

  const handleChangeRadio = ( e ) =>{
    // const SelectEvent = new CustomEvent('select_clothe', { 
    //   detail:{
    //     grupo: groupPrendas, 
    //     prenda: dataPrenda.mesh,
    //     material: dataPrenda.material,
    //   } 
    // });
    // // console.log("click", SelectEvent)
    // document.dispatchEvent(SelectEvent);
  }

  return (
    
    <Card control={<Radio />} sx={{ width: 120, m:1, display:'flex', justifyContent:'center', alignItems:'center' }} >
      <CustomCardActionArea onClick={handelClick} selected={selectedValue == dataPrenda.mesh} sx={{ width:'100%', height:'100%', p:1 }}>
        <CardMedia
          sx={{objectFit:'contain'}}
          component="img"
          height="90px"
          width="90%"
          image={require('../../../../assets/images/personalizacion/' + dataPrenda.imagen) }
        />
        <Typography gutterBottom variant="caption" sx={{ height:'2rem', textTransform:'uppercase', mt:2, mx:1 }} component="div">
            {dataPrenda.nombre}
        </Typography>
      </CustomCardActionArea>

      <FormControlLabel onChange={ handleChangeRadio } sx={{display:'none'}} ref={radioRef} value={dataPrenda.mesh} control={<Radio />}></FormControlLabel>
    </Card>
  );
}